.registration-popup__wrapper{
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: #03030e;
  left: 0px;
  top: 0px;
  z-index: 100000;
}

.registration-popup__inner{
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);

  
  .form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-left: 2em;
    padding-right: 2em;
    padding-bottom: 0.4em;
    background-color: #171717;
    border-radius: 25px;
    transition: .4s ease-in-out;
  }

  .form:hover {
    transform: scale(1.05);
    border: 1px solid black;
  }

  #heading {
    text-align: center;
    margin: 2em;
    color: rgb(255, 255, 255);
    font-size: 1.2em;
  }

  .field {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    border-radius: 25px;
    padding: 0.6em;
    border: none;
    outline: none;
    color: white;
    background-color: #171717;
    box-shadow: inset 2px 5px 10px rgb(5, 5, 5);
  }

  .button-wrapper{
    width: 70%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;

    label{
      display: flex;
    }

    label:nth-child(1){
      padding-right: 3px;
      border-right: 1px solid #fff;
    }

    .radioButton{
      width: 20px;
      height: 20px;
      margin: 0px 10px;
    }
  }

  .input-icon {
    height: 1.3em;
    width: 1.3em;
    fill: white;
  }

  .input-field {
    background: none;
    border: none;
    outline: none;
    width: 100%;
    color: #d3d3d3;
  }

  .form .btn {
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-top: 2.5em;
  }

  .button1 {
    padding: 0.5em;
    padding-left: 1.1em;
    padding-right: 1.1em;
    border-radius: 5px;
    margin-right: 0.5em;
    border: none;
    outline: none;
    transition: .4s ease-in-out;
    background-color: #252525;
    color: white;
    margin-bottom: 2em;
  }

  .button1:hover {
    background-color: black;
    color: white;
  }
}